*{margin:0;padding:0;box-sizing:border-box;font-family:'Poppins', sans-serif}a{text-decoration:none;color:inherit;cursor:pointer}button{border:none;cursor:pointer;background-color:transparent}button:active{transform:scale(0.99)}img{width:100%;object-fit:contain}h2{letter-spacing:0.8px}h4{letter-spacing:0.3px}p,span{letter-spacing:0.4px}ul{list-style:none}.chart circle{fill:none !important}.simple-cards-grid{display:grid;grid-template-columns:repeat(auto-fill, minmax(200px, 1fr));grid-gap:10px}.simple-card-preview{padding:10px;box-shadow:0 0 4px 1px #0000003b;cursor:pointer}.icon{width:50px;height:50px}.icon-lg{width:100px;height:100px}.main-layout{display:grid;grid-template-columns:250px 1fr 250px;grid-auto-flow:dense}.main-layout>*{grid-column:2}.main-layout>.full{grid-column:1 / -1}@media (max-width: 450px){.main-layout{grid-template-columns:minmax(20px, 20px) minmax(auto, 450px) minmax(20px, 20px)}}@media (max-width: 760px){.main-layout{grid-template-columns:minmax(20px, 1fr) minmax(auto, 760px) minmax(20px, 1fr)}}@media (max-width: 960px){.main-layout{grid-template-columns:minmax(20px, 200px) minmax(auto, 960px) minmax(20px, 200px)}}@media (min-width: 1200px){.main-layout{grid-template-columns:minmax(20px, 400px) minmax(auto, 1200px) minmax(20px, 400px)}}.header{display:flex;justify-content:space-around;background-color:#2b2a2b;color:#eee;padding:10px}.header a{border-radius:5px;padding:3px 7px}.header a.active{background-color:aqua}.header .icon-header{height:50px;width:50px}.contact-filter{padding:20px 0}.contact-filter input{width:100%;padding:4px 7px}.contact-list{display:grid;grid-template-columns:1fr;grid-gap:10px;gap:10px}.contact-list img{width:100px}.contact-preview{display:flex;align-items:center;justify-content:space-between}.contact-preview:hover{background-color:#cccccccc}.contact-preview a{display:flex;align-items:center}.contact-preview a h2{font-size:1.3rem}.contact-preview .actions{display:flex;align-items:center;grid-gap:20px;gap:20px}.contact-